  }
}
.content-container {
  max-width: 1080px;
  margin: 0 auto;
  padding: 0 16px;
  width: 100%;
}
.page-title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  width: 100%;
}
.transactions-container {
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  background-color: #fff;
  margin: 0 auto;
  width: 100%;
}
[data-theme="dark"] .transactions-container {
  background-color: #1e1e2d;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}
.MuiTable-root {
  min-width: 100%;
  width: 100%;
}
.categories-grid, 
.accounts-grid,
.budgets-grid {
  display: grid;
  gap: 24px;
  width: 100%;
  justify-content: center;
  margin: 0 auto;
}
.categories-grid {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}
@media (max-width: 600px) {
  .page-title-container {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }
  .content-container {
    padding: 0 12px;
  }
}
.budgets-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 24px;
  width: 100%;
  margin: 0 auto;
  justify-content: center;
}
@media (max-width: 767px) {
  .budgets-grid {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
}
@property --tw-rotate-x {
  syntax: "*";
  inherits: false;
}
@property --tw-rotate-y {
  syntax: "*";
  inherits: false;
}
@property --tw-rotate-z {
  syntax: "*";
  inherits: false;
}
@property --tw-skew-x {
  syntax: "*";
  inherits: false;
}
@property --tw-skew-y {
  syntax: "*";
  inherits: false;
}
@property --tw-border-style {
  syntax: "*";
  inherits: false;
  initial-value: solid;
}
@property --tw-font-weight {
  syntax: "*";
  inherits: false;
}
@property --tw-outline-style {
  syntax: "*";
  inherits: false;
  initial-value: solid;
}
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
@keyframes pulse {
  50% {
    opacity: 0.5;
  }
}
@layer properties {
  @supports ((-webkit-hyphens: none) and (not (margin-trim: inline))) or ((-moz-orient: inline) and (not (color:rgb(from red r g b)))) {
    *, ::before, ::after, ::backdrop {
      --tw-rotate-x: initial;
      --tw-rotate-y: initial;
      --tw-rotate-z: initial;
      --tw-skew-x: initial;
      --tw-skew-y: initial;
      --tw-border-style: solid;
      --tw-font-weight: initial;
      --tw-outline-style: solid;
    }
  }
}